html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fixed {
  overflow: hidden;
}
@media (min-width: 1100px) {
  body.scrollfix {
    padding-top: 90.69px;
  }
}

p {
  color: #000431;
  line-height: 1.5;
  font-size: 1.6rem;
}
@media (min-width: 500px) {
  p {
    font-size: 2.4rem;
  }
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: min(90%, 140rem);
  margin: 0 auto;
}

.header {
  background-image: url(../img/svg/darkbg.svg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 4rem 0;
}
.header h1 {
  color: #fff;
  font-weight: 700;
  text-align: center;
  margin: 0 0 2rem;
}
@media (min-width: 1100px) {
  .header h1 {
    margin: 0;
  }
}
.header .header__contenido {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1100px) {
  .header .header__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 1100px) {
  .header__nav {
    flex-direction: row;
    justify-content: flex-end;
  }
}
.header__nav .header__nav__enlace {
  color: #fff;
  font-size: 2.4rem;
  border-bottom: 1px solid #5934a0;
  padding: 1.2rem;
  width: 75%;
  text-align: center;
}
.header__nav .header__nav__enlace:last-child {
  border-bottom: none;
}
.header__nav .header__nav__enlace:hover {
  color: #ffb92a;
}
@media (min-width: 1100px) {
  .header__nav .header__nav__enlace {
    border-bottom: none;
    width: auto;
  }
}

@media (min-width: 1100px) {
  .fixed {
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    box-shadow: -3px 10px 27px -1px rgba(0, 0, 0, 0.4);
  }
}

.video {
  position: relative;
  height: 50rem;
  overflow: hidden;
}
@media (min-width: 1100px) {
  .video {
    height: 70rem;
  }
}
.video .video__overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(14, 3, 203);
  background: linear-gradient(33deg, rgba(14, 3, 203, 0.3031482857) 0%, rgba(252, 0, 255, 0.4992267171) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 1100px) {
  .video video {
    width: 100%;
  }
}

.video__overlay__text {
  z-index: 100;
}
.video__overlay__text h2 {
  color: #fff;
  font-size: 3rem;
  margin: 0;
}
@media (min-width: 350px) {
  .video__overlay__text h2 {
    font-size: 4rem;
  }
}
@media (min-width: 500px) {
  .video__overlay__text h2 {
    font-size: 5rem;
  }
}
@media (min-width: 700px) {
  .video__overlay__text h2 {
    font-size: 6rem;
  }
}
@media (min-width: 1100px) {
  .video__overlay__text h2 {
    font-size: 7rem;
  }
}
.video__overlay__text p {
  display: inline-block;
  background-color: #f53756;
  padding: 1.5rem 2rem;
  font-weight: 700;
  color: #fff;
  border-radius: 5px;
}
@media (min-width: 1100px) {
  .video__overlay__text p {
    padding: 1.5rem 5rem;
  }
}

.festival {
  display: grid;
  padding: 5rem 0;
  gap: 4rem;
}
@media (min-width: 1100px) {
  .festival {
    grid-template-columns: repeat(2, 1fr);
  }
}

.festival__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.festival__contenido h2 {
  margin-bottom: 0;
}
.festival__contenido .festival__contenido__fecha {
  border-radius: 5px;
  color: #fff;
  font-weight: 700;
  text-align: center;
  background-color: #f53756;
  text-align: center;
  padding: 1rem 0.5rem;
}
@media (min-width: 1100px) {
  .festival__contenido {
    text-align: left;
  }
  .festival__contenido .festival__contenido__fecha {
    text-align: center;
  }
}

.lineup {
  background-color: #f53756;
}
@media (min-width: 1100px) {
  .lineup {
    margin-top: -7rem;
  }
}
.lineup h2, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .lineup__dia {
  text-transform: uppercase;
  padding-top: 3rem;
  margin-bottom: 5rem;
  font-weight: 700;
}

.lineup__contenedor {
  display: grid;
  gap: 5rem;
}
@media (min-width: 1100px) {
  .lineup__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
.lineup__contenedor p {
  font-weight: 700;
}
.lineup__contenedor img {
  height: 100px;
  width: auto;
  margin: 0 auto;
  padding: 0 15%;
}

.lineup__contenedor__escenario {
  background-color: #5934a0;
  border-radius: 5px;
  padding-top: 2rem;
}
.lineup__contenedor__escenario li:nth-child(odd) {
  background-color: #44277a;
}

.lineup__contenedor__escenario-amarillo {
  background-color: #ffb92a;
}
.lineup__contenedor__escenario-amarillo li:nth-child(odd) {
  background-color: #f6a500;
}
@media (min-width: 300px) {
  .lineup__contenedor__escenario-amarillo li:nth-child(odd) {
    font-size: 1.8rem;
  }
}

.lineup__contenedor__escenario__calendario {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 2rem 0;
  color: #fff;
}
.lineup__contenedor__escenario__calendario li {
  padding: 2.5rem;
  font-weight: 900;
}
.lineup__contenedor__escenario__calendario li span {
  font-weight: 700;
}

.galeria {
  padding: 5rem 0;
}
.galeria h2 {
  text-align: center;
}

.galeria__imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 600px) {
  .galeria__imagenes {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1100px) {
  .galeria__imagenes {
    grid-template-columns: repeat(6, 1fr);
  }
}

.overlay {
  position: fixed;
  z-index: 200;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(0, 2, 24, 0.9);
}
.overlay picture {
  display: block;
  max-width: 100rem;
  padding: 5%;
}

.overlay__boton {
  background-color: #fff;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  width: 6rem;
  height: 6rem;
  font-weight: 700;
  color: #000431;
}

.boletos {
  background-color: #5934a0;
  padding-bottom: 5rem;
}
.boletos h2 {
  color: #fff;
  text-align: center;
}
@media (min-width: 1100px) {
  .boletos {
    margin-top: -12rem;
  }
}

@media (min-width: 1100px) {
  .boletos__info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.boletos__info__carta {
  padding-bottom: 5rem;
}
.boletos__info__carta__nombre {
  text-align: center;
  color: #fff;
}
.boletos__info__carta__header p, .boletos__info__carta__footer p {
  margin: 0;
  text-align: center;
  color: #fff;
  padding: 1rem;
  font-weight: 700;
}
.boletos__info__carta__header {
  background-color: #f53756;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.boletos__info__carta__header p {
  text-transform: uppercase;
}
.boletos__info__carta__body {
  background-color: #fff;
}
.boletos__info__carta__body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.boletos__info__carta__body ul li {
  padding: 2rem;
  border-bottom: 1px solid #e1e1e1;
}
.boletos__info__carta__body ul li:last-child {
  border: none;
}
.boletos__info__carta__footer {
  background-color: #ffb92a;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.boletos__info__carta__footer p {
  font-size: 3rem;
}

.footer {
  background-color: #f53756;
}
.footer p {
  color: #fff;
  text-align: center;
  margin: 0;
  padding: 3rem;
  font-size: 1.6rem;
}